import React, { useState, useEffect } from 'react'
import { InputNumber, Table } from 'antd';

const TableMaterialVendor = (props) => {

    const [quantity, setQuantity] = useState([])
    const [price, setPrice] = useState([])
    const [selectedRowKeys, setSelectedRowKeys] = useState([])

    const { view, selectedMaterial, selectedMaterialDelete } = props

    useEffect(() => {
        props.handleChangeQuantity(quantity)
    }, [quantity])

    useEffect(() => {
        props.handleChangePrice(price)
    }, [price])

    useEffect(() => {
        if (props.selectedMaterial.length > 0) {
            let newQuantity = [...quantity]
            let newPrice = [...price]
            selectedMaterial.map((item) => {
                newQuantity[item.id] = item.quantity
                setQuantity(newQuantity)

                newPrice[item.id] = item.price
                setPrice(newPrice)
            })
        }

    }, [props.selectedMaterial])

    const handleChangeQuantity = (id, val) => {
        let newQuantity = [...quantity]
        newQuantity[id] = val
        setQuantity(newQuantity)
    }

    const handleChangePrice = (id, val) => {
        let newPrice = [...price]
        newPrice[id] = val
        setPrice(newPrice)
    }

    //on check table item
    const onSelectChange = (selectedRowKeys) => {
        setSelectedRowKeys(selectedRowKeys)
        selectedMaterialDelete(selectedRowKeys)
    }

    const rowSelection = {
        selectedRowKeys,
        onChange: onSelectChange,
    };

    const columns = [
        {
            title: 'Material Code',
            dataIndex: 'material_code',
            width: 200
        },
        {
            title: 'Price (Rp)',
            dataIndex: 'id',
            render: (id) => {
                const { status } = props.formData

                let valuePrice = price[id] ? price[id] : 0
                return <InputNumber
                    min={0}
                    size='small'
                    style={{ width: '70%' }}
                    decimalSeparator={','}
                    formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, '.')}
                    parser={value => `${value}`.replace(/(\.*)|/g, '')}
                    readOnly={status !== 0} //must waiting approve
                    value={valuePrice}
                    onChange={(id, (val) => { handleChangePrice(id, val) })}
                />
            },
            width: 200
        },
        {
            title: 'Quantity',
            dataIndex: 'id',
            render: (id, record) => {
                let quantityUom = null
                let qty = quantity[id] ? quantity[id] : 0

                if (record.quantity_uom) {
                    quantityUom = record.quantity_uom > 1 && ` (${record.quantity_uom * qty})`
                } else {
                    quantityUom = record.material.quantity_uom > 1 && ` (${record.material.quantity_uom * qty})`
                }

                return <>
                    <InputNumber
                        min={0}
                        size='small'
                        readOnly={props.view}
                        value={qty}
                        onChange={(id, (val) => { handleChangeQuantity(id, val) })} />

                    {quantityUom}
                </>
            },
            width: 150
        },
        {
            title: 'Subtotal (Rp)',
            dataIndex: 'id',
            render: (id) => {
                let qty = quantity[id] ? quantity[id] : 0
                let valuePrice = price[id] ? price[id] : 0
                let subtotal = parseInt(valuePrice) * parseInt(qty)

                return <InputNumber
                    min={0}
                    size='small'
                    style={{ width: '70%' }}
                    decimalSeparator={','}
                    formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, '.')}
                    parser={value => `${value}`.replace(/(\.*)|/g, '')}
                    readOnly={true}
                    value={subtotal}
                />
            },
            width: 200
        },
        {
            title: 'UoM',
            dataIndex: 'uom',
            render: (uom) => {
                if (uom) { return uom.name }
            },
            width: 100
        },
    ];

    if (!view) {
        delete columns[1]
        delete columns[3]
    }

    return (
        <Table
            rowSelection={!view && rowSelection}
            rowKey={record => record.id}
            dataSource={selectedMaterial}
            columns={columns}
            pagination={false}
            size='small'
            scroll={{ y: 300, x: 1000 }} />
    )
}

export default TableMaterialVendor;