import React, { useState, useEffect } from 'react'
import { Form, Row, Col, Button, Select, Input, DatePicker, Tooltip, Divider, Space, message } from 'antd';
import Api from '../../helpers/api';
import moment from 'moment';

import {
    BarsOutlined,
    ArrowLeftOutlined,
    ReloadOutlined,
    ArrowRightOutlined
} from '@ant-design/icons';
import handleApiError from '../../helpers/handleApiError';
import NoteModal from '../../components/Modal/NoteModal';

const api = new Api();
const { Option } = Select;

const FormReservation = (props) => {

    const [errors, setErrors] = useState({})
    const [loading, setLoading] = useState(false)
    const [form] = Form.useForm();
    const [inputDisabled, setInputDisabled] = useState(false)

    const [formData, setFormData] = useState({
        code: `RES/${moment(new Date()).format("YYMMDD/HHmmss")}`,
        type: '',
        plant_id: '',
        plant_sender: '',
        room_id: '',
        room_sender: '',
        note: '',
        receiver: '',
        sender: '',
    })

    const [select, setSelect] = useState({
        plant: [],
        plant_sender: [],
        room: [],
        room_sender: [],
        vendor: [],
    })

    const [showModal, setShowModal] = useState({
        modalNote: false,
    })

    useEffect(() => {
        const { add } = props

        if (add) {
            const { type, code } = formData
            form.setFieldsValue({ code, type });
        }

        fetchPlant()
    }, []);

    useEffect(() => {
        if (formData.type === 0) {
            fetchVendor()
        }
    }, [formData.type]);

    useEffect(() => {
        if (formData.plant_id) {
            fetchRoom('room', formData.plant_id)
        }
    }, [formData.plant_id]);

    useEffect(() => {
        if (formData.plant_sender) {
            fetchRoom('room_sender', formData.plant_sender)
        }
    }, [formData.plant_sender]);

    useEffect(() => {
        const { view } = props

        if (view) {
            setInputDisabled(true)
            setFormData(props.formData)
            form.setFieldsValue(props.formData);
        }
    }, [props.formData]);

    const onFinish = (data) => {
        setLoading(true)

        data['delivery_date'] = formData.delivery_date
        data['note'] = formData.note
        let apiPost = api.storeReservation(data)

        apiPost.then(response => {
            console.log(response)
            setLoading(false)
            data['response'] = response.data
            props.valueFormData(data)
            setInputDisabled(true)
            setErrors({})
            // message.success('save data success');
            // props.history.goBack();
        }).catch(error => {
            setLoading(false)
            if (error.response && error.response.data) {
                const { errors } = error.response.data
                setErrors(errors)
                // message.error(message);
            }
            handleApiError(error, props)
        });
    };

    const fetchPlant = (name, q) => {
        api.getPlant({ q: q }).then(response => {
            const { data } = response.data;
            let newSelect = { ...select };

            if (name) {
                newSelect[name] = data;
                setSelect(newSelect)
            } else {
                newSelect.plant = data;
                newSelect.plant_sender = data;
                setSelect(newSelect)
            }

        }).catch(err => {
            console.log(err)
            // handleApiError(err, props)
        })
    };

    const handleSearchPlant = (name, q) => {
        fetchPlant(name, q)
    }

    const handleChangePlant = (name, value) => {
        const newFormData = { ...formData };
        newFormData[name] = value;

        let responsible = name == 'plant_id' ? 'receiver' : 'sender';
        newFormData[responsible] = null;

        setFormData(newFormData)

        let room = name == 'plant_id' ? { room_id: '' } : { room_sender: '' };
        form.setFieldsValue({ ...room });
        // fetchPlant()
    }

    const fetchVendor = (q) => {
        api.getVendor({ q: q }).then(response => {
            const { data } = response.data;
            setSelect({ ...select, ['vendor']: data })
        }).catch(err => {
            console.log(err)
            // handleApiError(err, props)
        })
    };

    const handleSearchVendor = (q) => {
        fetchVendor(q)
    }

    const fetchRoom = (name, value, q) => {
        api.getRoom({ plant_id: [value], q })
            .then(response => {
                const { data } = response.data;

                const newSelect = { ...select };
                newSelect[name] = data;
                setSelect(newSelect)
            }).catch(err => {
                console.log(err)
                // handleApiError(err, props)
            })
    };

    const handleSearchRoom = (name, plantName, q) => {
        fetchRoom(name, formData[plantName], q)
    }

    const handleChangeRoom = (name, roomName, value) => {
        let index = select[roomName].map(item => item.id).lastIndexOf(value)
        const newFormData = { ...formData };
        newFormData[name] = value;

        let responsible = name == 'room_id' ? 'receiver' : 'sender';
        newFormData[responsible] = select[roomName][index].responsible_person && `${select[roomName][index].responsible_person.fullname} - ${select[roomName][index].responsible_person.email}`;

        setFormData(newFormData)
    }

    const handleInput = (value, name) => {
        setFormData({ ...formData, [name]: value })
    }

    const handleShowModal = (name, value) => {
        setShowModal({ ...showModal, [name]: value })
    }

    const handleReset = () => {
        let field = {
            type: '',
            plant_id: '',
            plant_sender: '',
            room_id: '',
            room_sender: '',
            note: '',
            receiver: '',
            sender: '',
            delivery_date: ''
        }

        form.setFieldsValue(field)
        setFormData(field)
    }

    return (
        <Form name="complex-form" onFinish={onFinish} labelCol={{ span: 8 }} wrapperCol={{ span: 24 }}
            form={form}>
            <Row>
                <Col md={12} >
                    <Form.Item name="code" label="Code"
                        validateStatus={errors && errors.code ? 'error' : undefined}
                        help={errors ? errors.code : null}
                        rules={[{ required: true, message: 'Please input code' }]}>
                        <Input disabled={true} />
                    </Form.Item>

                    <Form.Item name="type" label="Type"
                        validateStatus={errors && errors.type ? 'error' : undefined}
                        help={errors ? errors.type : null}
                        rules={[{ required: !inputDisabled, message: 'Please select type' }]}>
                        <Select
                            disabled={inputDisabled}
                            onChange={(value) => handleInput(value, 'type')}
                        >
                            <Option value={0}>Request to Vendor</Option>
                            {/* <Option value={1}>Transfer with Do</Option>
                            <Option value={2}>Transfer without Do</Option> */}
                        </Select>
                    </Form.Item>

                    <Form.Item colon={false} label="Receiver" style={{ fontWeight: "bold" }} />
                    <Form.Item name="plant_id" label="Plant"
                        validateStatus={errors && errors.plant_id ? 'error' : undefined}
                        help={errors ? errors.plant_id : null}
                        rules={[{ required: !inputDisabled, message: 'Please input plant' }]}>
                        <Select
                            disabled={inputDisabled}
                            filterOption={false}
                            showSearch
                            onSearch={(q) => handleSearchPlant('plant', q)}
                            onChange={(value) => handleChangePlant('plant_id', value)}>

                            {select.plant.map(item => (
                                <Option key={item.id} value={item.id}>{item.name}</Option>
                            ))}
                        </Select>
                    </Form.Item>

                    <Form.Item name="room_id" label="Room"
                        validateStatus={errors && errors.room_id ? 'error' : undefined}
                        help={errors ? errors.room_id : null}
                        rules={[{ required: !inputDisabled, message: 'Please input room' }]}>
                        <Select
                            disabled={inputDisabled}
                            filterOption={false}
                            showSearch
                            onSearch={(q) => handleSearchRoom('room', 'plant_id', q)}
                            onChange={(value) => handleChangeRoom('room_id', 'room', value)}>

                            {select.room.map(item => (
                                <Option key={item.id} value={item.id}>{item.name}</Option>
                            ))}
                        </Select>
                    </Form.Item>

                    <Form.Item label="Responsible Person">
                        {formData.receiver}
                    </Form.Item>
                </Col>
                <Col md={12}>

                    <Form.Item colon={false} label="Sender" style={{ fontWeight: "bold" }} />

                    {formData.type !== ''
                        ?
                        formData.type === 0
                            ?
                            <Form.Item name="vendor_id" label="Vendor"
                                validateStatus={errors && errors.vendor_id ? 'error' : undefined}
                                help={errors ? errors.vendor_id : null}
                                rules={[{ required: !inputDisabled, message: 'Please input plant' }]}>
                                <Select
                                    disabled={inputDisabled}
                                    filterOption={false}
                                    showSearch
                                    onSearch={handleSearchVendor}
                                    onChange={(value) => handleInput(value, 'vendor_id')}>

                                    {select.vendor.map(item => (
                                        <Option key={item.id} value={item.id}>{item.name}</Option>
                                    ))}
                                </Select>
                            </Form.Item>
                            :
                            <div>
                                <Form.Item name="plant_sender" label="Plant"
                                    validateStatus={errors && errors.plant_sender ? 'error' : undefined}
                                    help={errors ? errors.plant_sender : null}
                                    rules={[{ required: !inputDisabled, message: 'Please input plant' }]}>
                                    <Select
                                        disabled={inputDisabled}
                                        filterOption={false}
                                        showSearch
                                        onSearch={(q) => handleSearchPlant('plant_sender', q)}
                                        onChange={(value) => handleChangePlant('plant_sender', value)}>

                                        {select.plant_sender.map(item => (
                                            <Option key={item.id} value={item.id}>{item.name}</Option>
                                        ))}
                                    </Select>
                                </Form.Item>

                                <Form.Item name="room_sender" label="Room"
                                    validateStatus={errors && errors.room_sender ? 'error' : undefined}
                                    help={errors ? errors.room_sender : null}
                                    rules={[{ required: !inputDisabled, message: 'Please input room' }]}>
                                    <Select
                                        disabled={inputDisabled}
                                        filterOption={false}
                                        showSearch
                                        onSearch={(q) => handleSearchRoom('room_sender', 'plant_sender', q)}
                                        onChange={(value) => handleChangeRoom('room_sender', 'room_sender', value)}>

                                        {select.room_sender.map(item => (
                                            <Option key={item.id} value={item.id}>{item.name}</Option>
                                        ))}
                                    </Select>
                                </Form.Item>

                                <Form.Item label="Responsible Person">
                                    {formData.sender}
                                </Form.Item>
                            </div>
                        : null
                    }

                    <Form.Item name="delivery_date" label="Req. Delivery Date"
                        validateStatus={errors && errors.room_id ? 'error' : undefined}
                        help={errors ? errors.room_id : null}
                        rules={[{ required: !inputDisabled, message: 'Please input req. delivery date' }]}>
                        <DatePicker style={{ width: '100%' }}
                            disabled={inputDisabled}
                            onChange={(date, dateString) => handleInput(dateString, 'delivery_date')} />
                    </Form.Item>

                    <Form.Item label="Note"
                        validateStatus={errors && errors.note ? 'error' : undefined}
                        help={errors ? errors.note : null}
                        rules={[{ required: false, message: 'Please input note' }]}>
                        <Tooltip title="Note">
                            <Button icon={<BarsOutlined />}
                                onClick={() => handleShowModal('modalNote', true)}
                            />
                        </Tooltip>
                        <NoteModal
                            showNoteModal={showModal.modalNote}
                            handleShowModal={handleShowModal}
                            data={formData.note}
                            handleInput={handleInput}
                            modalName='modalNote'
                            formName='note'
                            updateAction={false}
                        />
                    </Form.Item>
                </Col>
            </Row>

            {!inputDisabled ?
                <>
                    <br />
                    <p><span style={{ color: '#f5222d', fontSize: 14 }}>*</span> Required</p>
                    <Divider />

                    <Space>
                        <Button onClick={() => props.history.goBack()}
                            icon={<ArrowLeftOutlined />}
                        >Back</Button>

                        <Button
                            onClick={() => handleReset()}
                            icon={<ReloadOutlined />}
                        >Reset</Button>

                        <Button type="primary" htmlType="submit"
                            loading={loading}
                            icon={<ArrowRightOutlined />}
                        >Next</Button>
                    </Space>
                </>
                : null
            }
        </Form >
    )
}

export default FormReservation;