import React, { useState, useEffect } from 'react'
import { Row, Col, Card } from 'antd';
import moment from 'moment';

import FormReservation from './FormReservation';
import FormMaterial from './FormMaterial';

import Api from '../../helpers/api';
import handleApiError from '../../helpers/handleApiError';

const api = new Api();

const FormPage = (props) => {

    const [formData, setFormData] = useState({})

    useEffect(() => {
        const { view } = props

        if (view) {
            getViewData()
        }
    }, []);

    const valueFormData = (value) => {
        setFormData(value)
    }

    const getViewData = () => {
        const { id } = props.match.params

        api.viewReservation(id)
            .then(response => {
                const {
                    code, type, plant_id, room_id, room_receiver,
                    room_sender, note,
                    vendor_id, delivery_date, details, status, total_price
                } = response.data
                setFormData({
                    code,
                    type,
                    vendor_id,
                    note,
                    delivery_date: moment.utc(delivery_date, 'YYYY-MM-DD'),
                    total_price,
                    details,
                    status,

                    plant_id, //plant receiver
                    room_id, //room receiver
                    receiver: room_receiver ? room_receiver.responsible_person.fullname : '',

                    plant_sender: room_sender ? room_sender.plant.id : null,
                    room_sender: room_sender ? room_sender.id : null,
                    sender: room_sender ? room_sender.responsible_person.fullname : '',
                })
            }).catch(error => {
                handleApiError(error, props)
            })
    }

    return (
        <Card>
            <Row>
                <Col xs={24} sm={24} md={24} lg={18} xl={18}>
                    <FormReservation
                        {...props}
                        formData={formData}
                        valueFormData={valueFormData}
                    />
                </Col>
                {formData && formData.room_id
                    ?
                    <Col xs={24} sm={24} md={24} lg={24} xl={24}>
                        <FormMaterial
                            {...props}
                            formData={formData}
                        />
                    </Col>
                    : null}
            </Row>
        </Card >
    )
}

export default FormPage;