import React, { useState, useEffect } from 'react'
import { InputNumber, Table } from 'antd';

const TableMaterialRoom = (props) => {

    const [quantity, setQuantity] = useState([])
    const [selectedRowKeys, setSelectedRowKeys] = useState([])

    useEffect(() => {
        props.handleChangeQuantity(quantity)
    }, [quantity])

    useEffect(() => {
        if (props.selectedMaterial.length > 0) {
            let newQuantity = [...quantity]
            props.selectedMaterial.map((item) => {
                newQuantity[item.id] = item.quantity
                setQuantity(newQuantity)
            })
        }

    }, [props.selectedMaterial])

    const handleChangeQuantity = (id, val) => {
        let newQuantity = [...quantity]
        newQuantity[id] = val
        setQuantity(newQuantity)
    }

    //on check table item
    const onSelectChange = (selectedRowKeys) => {
        setSelectedRowKeys(selectedRowKeys)
        props.selectedMaterialDelete(selectedRowKeys)
    }

    const rowSelection = {
        selectedRowKeys,
        onChange: onSelectChange,
    };

    const columns = [
        {
            title: 'Material Code',
            dataIndex: 'material',
            render: (material) => {
                if (material) return material.material_code
            },
            width: 200
        },
        {
            title: 'Quantity',
            dataIndex: 'id',
            render: (id) => (
                <InputNumber
                    min={0}
                    size='small'
                    readOnly={props.view}
                    value={quantity[id] ? quantity[id] : 0}
                    onChange={(id, (val) => { handleChangeQuantity(id, val) })} />
            ),
            width: 150
        },
        {
            title: 'Description',
            dataIndex: 'material',
            render: (material) => {
                if (material) return material.description
            },
            width: 200
        },
        {
            title: 'UoM',
            dataIndex: 'material',
            render: (material) => {
                if (material.uom) { return material.uom.name }
            },
            width: 100
        },
    ];
    return (
        <Table
            rowSelection={rowSelection}
            rowKey={record => record.id}
            dataSource={props.selectedMaterial}
            columns={columns}
            pagination={false}
            size="small"
            bordered
            scroll={{ x: 1000 }} />
    )
}

export default TableMaterialRoom;