import React, { useState, useEffect } from 'react'
import { Row, Col, Button, Space, message, Tooltip, Modal } from 'antd';
import Api from '../../helpers/api';

import {
    PlusOutlined,
    DeleteOutlined,
    SaveOutlined,
    ArrowLeftOutlined,
    ReloadOutlined,
    CheckOutlined,
    CloseCircleOutlined
} from '@ant-design/icons';
import TableMaterialVendor from './TableMaterialVendor';
import TableMaterialRoom from './TableMaterialRoom';
import MaterialModal from '../../components/Modal/MaterialModal';
import StockModal from '../../components/Modal/StockModal';
import handleApiError from '../../helpers/handleApiError';
import hasAuthObject from '../../helpers/hasAuthObject';
import NoteModal from '../../components/Modal/NoteModal';

const api = new Api();

const authobjectCfg = {
    approve: 'reservation-approve',
    reject: 'reservation-reject',
}

const FormMaterial = (props) => {

    const [errors, setErrors] = useState({})
    const [loading, setLoading] = useState(false)
    // const [showModal, setShowModal] = useState(false)

    const [showModal, setShowModal] = useState({
        modalMaterial: false,
        modalRejectNote: false,
    })
    const [selectedMaterial, setSelectedMaterial] = useState([])
    const [quantity, setQuantity] = useState([])
    const [price, setPrice] = useState([])
    const [total, setTotal] = useState(0)
    const [selectedMaterialDelete, setSelectedMaterialDelete] = useState([])
    const [activeButtonMaterialDelete, setActiveButtonMaterialDelete] = useState(true)
    const [remark, setRemark] = useState('')

    const { formData, view } = props

    useEffect(() => {
        if (formData.details) {
            setSelectedMaterial(formData.details)
            setTotal(parseInt(formData.total_price))
        }
    }, [formData])

    const handleInput = (value, name) => {
        setRemark(value)
    }

    const handleShowModal = (type, value) => {
        setShowModal({ ...formData, [type]: value })
    }

    const handleChangePrice = (value) => {
        setPrice(value)

        let totalPrice = []
        let valueQuantity = 0
        let valuePrice = 0
        let subtotal = 0
        quantity.map((item, index) => {
            if (value[index]) {
                valueQuantity = quantity[index] ? quantity[index] : 0
                valuePrice = value[index] ? value[index] : 0
                subtotal = valueQuantity * valuePrice
                totalPrice.push(subtotal)
            }
        })
        let total = totalPrice.reduce((a, b) => a + b, 0)
        if (view && formData.status == 0) {
            setTotal(total)
        }
    }

    const handleChangeQuantity = (value) => {
        setQuantity(value)
    }

    const handleSelectedMaterialDelete = (value) => {
        setSelectedMaterialDelete(value)
        setActiveButtonMaterialDelete(value.length > 0 ? false : true)
    }

    const handleDeleteSelectedMaterial = () => {
        let newSelectedMaterial = [...selectedMaterial]
        newSelectedMaterial = newSelectedMaterial.filter(val => !selectedMaterialDelete.includes(val.id));
        setSelectedMaterial(newSelectedMaterial)
        setActiveButtonMaterialDelete(true)
    }

    const materialSelected = (value) => {
        setSelectedMaterial(value)
    }

    const handleOk = () => {
        handleShowModal('modalMaterial', false);
    };

    const handleReject = () => {
        Modal.confirm({
            title: `Are you sure reject this data?`,
            okText: 'Yes',
            okType: 'danger',
            cancelText: 'No',
            onOk: () => {
                handleShowModal('modalRejectNote', true)
            },
            onCancel: () => {
                console.log('Cancel');
            },
        });
    }

    const handleRejectAction = (isReject) => {
        if (isReject) {
            const { id } = props.match.params
            api.rejectReservation(id, { remark })
                .then(response => {
                    props.history.push('/reservation/list');
                    message.success('success rejected');
                }).catch(error => {
                    if (error.response && error.response.data) {
                        const { errors } = error.response.data
                        setErrors(errors)

                        if (errors && errors.material) {
                            message.error(errors.material);
                        }
                    }

                    handleApiError(error, props)
                })
        }
    }

    const handleApprove = () => {
        Modal.confirm({
            title: `Are you sure approve this data?`,
            okText: 'Yes',
            okType: 'primary',
            cancelText: 'No',
            onOk: () => {
                handleApproveAction()
            },
            onCancel: () => {
                console.log('Cancel');
            },
        });
    }

    const handleApproveAction = () => {
        const { id } = props.match.params

        let data = { quantity, price }

        api.approveReservation(id, data)
            .then(response => {
                props.history.push('/reservation/list');
                message.success('success approved');
            }).catch(error => {
                if (error.response && error.response.data) {
                    const { errors } = error.response.data
                    setErrors(errors)

                    if (errors && errors.material) {
                        message.error(errors.material);
                    }
                }

                handleApiError(error, props)
            })
    }

    const handleSubmit = () => {
        selectedMaterial.map((item, index) => {
            selectedMaterial[index].reservation_id = formData.response.id
            selectedMaterial[index].quantity = quantity[item.id] ? quantity[item.id] : 0

            if (selectedMaterial[index]['material']) {
                selectedMaterial[index].id = selectedMaterial[index].material.id
            }
        })

        let data = { materials: selectedMaterial }
        let apiPost = api.storeReservationDetail(data)

        setLoading(true)
        apiPost.then(response => {
            setLoading(false)
            message.success('save data success');
            props.history.goBack();
        }).catch(error => {
            setLoading(false)
            if (error.response && error.response.data) {
                const { errors } = error.response.data
                setErrors(errors)
            }
            handleApiError(error, props)
        });
    }

    const handleReset = () => {
        setSelectedMaterialDelete(selectedMaterial.map(item => item.id))
        setSelectedMaterial([])
        setQuantity([])
    }

    return (
        <Space direction="vertical" size="large" style={{ width: '100%' }}>
            <Row>
                {view ?? (
                    <Col xs={24} sm={24} md={24} lg={24} xl={24}>
                        <Space>
                            <Button type="primary"
                                onClick={() => handleShowModal('modalMaterial', true)}
                                icon={<PlusOutlined />}
                            />
                            <Button
                                danger
                                disabled={activeButtonMaterialDelete}
                                onClick={handleDeleteSelectedMaterial}
                                icon={<DeleteOutlined />}
                            />
                            {errors &&
                                <div className="ant-form-item-explain ant-form-item-explain-error">
                                    <div role="alert">
                                        {Object.values(errors)}
                                    </div>
                                </div>}
                        </Space>
                    </Col>
                )}

                {view && formData.status == 0 ? ( //status waiting approve
                    <Col xs={24} sm={24} md={24} lg={24} xl={24}>
                        <Space>
                            {hasAuthObject(authobjectCfg.approve) && (
                                <Tooltip title='Approve'>
                                    <Button
                                        type='primary'
                                        onClick={handleApprove}
                                        icon={<CheckOutlined />}
                                    />
                                </Tooltip>
                            )}
                            {hasAuthObject(authobjectCfg.reject) && (
                                <>
                                    <Tooltip title='Reject'>
                                        <Button
                                            danger
                                            onClick={handleReject}
                                            icon={<CloseCircleOutlined />}
                                        />
                                    </Tooltip>
                                    <NoteModal
                                        showNoteModal={showModal.modalRejectNote}
                                        handleShowModal={handleShowModal}
                                        data={remark}
                                        handleInput={handleInput}
                                        modalName='modalRejectNote'
                                        formName='remark'
                                        updateAction={handleRejectAction}
                                    />
                                </>
                            )}
                        </Space>
                    </Col>
                ) : null}

                {view && <h2 style={{ marginTop: 20 }}>Total Price: {`${total}`.replace(/\B(?=(\d{3})+(?!\d))/g, '.')}</h2>}
            </Row>
            <Row>
                <Col xs={24} sm={24} md={24} lg={24} xl={24}>

                    {formData.type === 0
                        ?
                        <TableMaterialVendor
                            {...props}
                            selectedMaterial={selectedMaterial}
                            handleChangeQuantity={handleChangeQuantity}
                            handleChangePrice={handleChangePrice}
                            selectedMaterialDelete={handleSelectedMaterialDelete} />
                        :
                        <TableMaterialRoom
                            {...props}
                            selectedMaterial={selectedMaterial}
                            handleChangeQuantity={handleChangeQuantity}
                            selectedMaterialDelete={handleSelectedMaterialDelete} />
                    }
                </Col>
            </Row>

            <Space>
                <Button onClick={() => props.history.goBack()}
                    icon={<ArrowLeftOutlined />}
                >Back</Button>

                {view ??
                    <>
                        <Button
                            onClick={() => handleReset()}
                            icon={<ReloadOutlined />}
                        >Reset</Button>

                        <Button type="primary" htmlType="submit"
                            onClick={handleSubmit}
                            loading={loading}
                            icon={<SaveOutlined />}
                        >Save</Button>
                    </>
                }
            </Space>

            <Modal
                title="Select Material"
                open={showModal.modalMaterial}
                onOk={handleOk}
                cancelButtonProps={{ style: { display: 'none' } }}
                width={1000}>

                {formData.type === 0
                    ?
                    <MaterialModal
                        materialSelected={materialSelected}
                        selectedMaterialDelete={selectedMaterialDelete}
                    />
                    :
                    <StockModal
                        materialSelected={materialSelected}
                        selectedMaterialDelete={selectedMaterialDelete}
                        roomId={formData.room_sender}
                    />
                }
            </Modal>
        </Space>
    )
}

export default FormMaterial;